/* General Styles */
body, html {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Container for the whole app */
  .ArticleGenerator {
    max-width: 800px;
    margin: 20px auto;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  /* Input Section Styles */
  .input-section {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }
  
  .input-section label {
    margin-bottom: 5px;
  }
  
  .input-section input,
  .input-section select,
  .input-section button {
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  /* Title Section Styles */
  .title-section {
    text-align: center;
    margin-bottom: 20px;
  }
  
  /* Sections Container Styles */
  .sections-container {
    border-top: 1px solid #eee;
    padding-top: 20px;
  }
  
  .section {
    margin-bottom: 10px;
  }
  
  .section-content {
    background: #f9f9f9;
    border-left: 3px solid #007bff;
    padding: 10px;
    margin-bottom: 5px;
  }
  
  /* Button Styles */
  button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 5px;
  }
  
  button:disabled {
    background-color: #ccc;
  }
  
  /* Loading Overlay Styles */
  .loading-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  /* Error message styles */
  .error-message {
    color: red;
    margin-bottom: 10px;
  }
  
  /* Combined Content Styles */
  .combined-content {
    background: #f9f9f9;
    border-left: 3px solid #28a745;
    padding: 10px;
    margin-top: 20px;
  }
  
  /* Social Output Section Styles */
  .social-output-section {
    margin-top: 20px;
  }
  .combined-content {
    margin: 20px;
    padding: 20px;
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.article-content {
    margin-bottom: 20px;
}

.combined-content button {
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.combined-content button:disabled {
    background-color: #cccccc;
}

  